import { useTranslation } from "react-i18next";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import style from "../../../MembersTab/student/Mainbody/StudentClass/Exam/Mcq/StudentMCQ.module.css";
import { useEffect, useState } from "react";
import {
  useGetStudentInternalEvaluationTestDetail,
  useStudentInternalEvaluationSaveQuestion,
  useStudentInternalEvaluationSubmitTest,
} from "../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import Duration from "../../../MembersTab/student/Mainbody/StudentClass/Exam/Mcq/Duration";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../Services/UrlConfig/AssestsBaseUrl";

const GiveStudentInternalEvaluation = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [autoSubmit, setAutoSubmit] = useState(false);

  const [allQuestion, setAllQuestion] = useState([]);
  const [givenQuestionId, setGivenQuestionId] = useState([]);
  const [givenQuestionAnswer, setGivenQuestionAnswer] = useState([]);
  const [oneQuestion, setOneQuestion] = useState("");
  const [indexCount, setIndexCount] = useState(0);
  const [studentInternalEvaluationSaveQuestion] =
    useStudentInternalEvaluationSaveQuestion();
  const [studentInternalEvaluationSubmitTest] =
    useStudentInternalEvaluationSubmitTest();

  const { getStudentInternalEvaluationTestDetail } =
    useGetStudentInternalEvaluationTestDetail({
      data: {
        stid: getQuery.state?.tsid,
      },
      skip: !getQuery.state?.tsid,
    });

  // console.info("thghsdgfs", getStudentInternalEvaluationTestDetail);

  useEffect(() => {
    if (getStudentInternalEvaluationTestDetail) {
      setAllQuestion(getStudentInternalEvaluationTestDetail?.questions);
      setOneQuestion(getStudentInternalEvaluationTestDetail?.questions[0]);
      let arr = [];
      for (let args of getStudentInternalEvaluationTestDetail?.questions) {
        arr.push({
          _id: args?._id,
          givenAnswer: args.givenAnswer,
        });
        if (args.givenAnswer?.length) {
          setGivenQuestionId((prev) => [...prev, args?._id]);
        }
      }
      setGivenQuestionAnswer((answer) => [...answer, ...arr]);
    }
  }, [getStudentInternalEvaluationTestDetail]);

  useEffect(() => {
    setOneQuestion(
      getStudentInternalEvaluationTestDetail?.questions[indexCount]
    );
  }, [indexCount]);

  useEffect(() => {
    if (autoSubmit) onSubmitAnswer();
  }, [autoSubmit]);

  const onCorrectAnswer = (val, val2) => {
    // console.info("this sdffg set", oneQuestion);

    if (val2) {
      setOneQuestion((quest) => ({
        ...quest,
        givenAnswer: [val],
      }));
      if (givenQuestionId?.includes(oneQuestion?._id)) {
        const alreadyAnswer = givenQuestionAnswer;
        for (let ans of alreadyAnswer) {
          if (ans?._id === oneQuestion?._id) {
            ans.givenAnswer = [val];
          }
        }
        setGivenQuestionAnswer(alreadyAnswer);
      } else {
        setGivenQuestionId((given) => [...given, oneQuestion?._id]);
        setGivenQuestionAnswer((answer) => [
          ...answer,
          {
            _id: oneQuestion?._id,
            givenAnswer: [val],
          },
        ]);
      }
    } else {
      setOneQuestion((quest) => ({
        ...quest,
        givenAnswer: [],
      }));
      setGivenQuestionId(
        givenQuestionId?.filter((val) => val !== oneQuestion?._id)
      );
      setGivenQuestionAnswer(
        givenQuestionAnswer?.filter((val) => val?._id !== oneQuestion?._id)
      );
    }

    studentInternalEvaluationSaveQuestion({
      stid: getQuery.state?.tsid,
      bodyParameter: {
        // "testSetLeftTime": 0,
        questionSNO: oneQuestion?.questionSNO,
        givenAnswer: [val],
        // notSelect:val2
      },
    })
      .then(() => {})
      .catch({});
  };

  const onSubmitAnswer = () => {
    if (getQuery.state?.notifyId && getQuery.state?.tsid) {
      studentInternalEvaluationSubmitTest({
        stid: getQuery.state?.tsid,
        bodyParameter: {
          testSetComplete: true,
          notifyId: getQuery.state?.notifyId,
        },
      })
        .then(() => {
          navigate(`/q/${params.username}/feed`);
        })
        .catch({});
    }
  };
  // console.info("feedbackRating", getQuery.state);
  return (
    <>
      <div className={style.studentMCQ}>
        <div className={style.studentMCQTop}>
          {getStudentInternalEvaluationTestDetail?.testDuration && (
            <Duration
              totalTime={
                getStudentInternalEvaluationTestDetail?.testDuration * 60
              }
              setAutoSubmit={setAutoSubmit}
            />
          )}
        </div>

        <div className={style.studentMCQBody}>
          <div className={style.studentMCQBodyLeft}>
            <div className={style.qTop}>
              <h6>Question {oneQuestion?.questionSNO}</h6>
              <h6>
                {oneQuestion?.questionNumber > 9
                  ? oneQuestion?.questionNumber
                  : `0${oneQuestion?.questionNumber}`}
              </h6>
            </div>

            <textarea
              className={style.show_question}
              readOnly
              rows={oneQuestion?.questionDescription?.split("\n").length}
              defaultValue={oneQuestion?.questionDescription}
            />

            {/* <p className={style.qtext}>{oneQuestion?.questionDescription}</p> */}
            {oneQuestion?.questionImage?.length ? (
              <img
                src={`${imageShowUrl}/${oneQuestion?.questionImage[0].documentKey}`}
                alt=""
                className={style.qImg}
              />
            ) : (
              ""
            )}

            <div className={oneQuestion?.qImg ? style.qOptions : style.noQImg}>
              {oneQuestion?.options?.map((opt, index) => (
                <div
                  className={
                    oneQuestion?.givenAnswer[0]?.optionNumber ===
                      opt?.optionNumber &&
                    givenQuestionAnswer?.filter(
                      (quest) =>
                        quest?._id === oneQuestion?._id &&
                        quest?.givenAnswer[0]?.optionNumber ===
                          opt?.optionNumber
                    )?.length
                      ? style.qOptionActive
                      : style.qOption
                  }
                >
                  <div
                    className={
                      oneQuestion?.givenAnswer[0]?.optionNumber ===
                        opt?.optionNumber &&
                      givenQuestionAnswer?.filter(
                        (quest) =>
                          quest?._id === oneQuestion?._id &&
                          quest?.givenAnswer[0]?.optionNumber ===
                            opt?.optionNumber
                      )?.length
                        ? style.qButtonActive
                        : style.qButton
                    }
                    onClick={() =>
                      onCorrectAnswer(
                        opt,
                        oneQuestion?.givenAnswer[0]?.optionNumber ===
                          opt?.optionNumber &&
                          givenQuestionAnswer?.filter(
                            (quest) =>
                              quest?._id === oneQuestion?._id &&
                              quest?.givenAnswer[0]?.optionNumber ===
                                opt?.optionNumber
                          )?.length
                          ? ""
                          : "another"
                      )
                    }
                  >
                    {index === 0
                      ? "A"
                      : index === 1
                      ? "B"
                      : index === 2
                      ? "C"
                      : index === 3
                      ? "D"
                      : "E"}
                  </div>
                  <p className={style.qOptionp}>{opt?.option}</p>
                </div>
              ))}
            </div>

            <div className={style.prevNextBtns}>
              <p onClick={() => setIndexCount((count) => count - 1)}>
                Previous
              </p>
              <p onClick={() => setIndexCount((count) => count + 1)}>Next</p>
            </div>
            {indexCount === allQuestion?.length - 1 && (
              <div className={style.submitMCQButtonContainer}>
                <div
                  className={style.submitMCQButtons}
                  onClick={onSubmitAnswer}
                  style={BUTTON_WRAPPER_STYLES}
                >
                  Submit
                </div>
              </div>
            )}
          </div>
          <div className={style.studentMCQBodyRight}>
            <div className={style.studentMCQProfile}>
              <div className={style.studentMCQProfileLeft}>
                <img
                  src={
                    getStudentInternalEvaluationTestDetail?.student
                      ?.studentProfilePhoto
                      ? `${imageShowUrl}/${getStudentInternalEvaluationTestDetail?.student?.studentProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="profile icon"
                />
              </div>
              <div className={style.studentMCQProfileRight}>
                <h6 className={style.studentMCQProfileRighth6}>
                  Name:{" "}
                  {`${
                    getStudentInternalEvaluationTestDetail?.student
                      ?.studentFirstName
                  } ${
                    getStudentInternalEvaluationTestDetail?.student
                      ?.studentMiddleName
                      ? getStudentInternalEvaluationTestDetail?.student
                          ?.studentMiddleName
                      : ""
                  } ${
                    getStudentInternalEvaluationTestDetail?.student
                      ?.studentLastName
                  }`}
                </h6>
                <p className={style.studentMCQProfileRightp}>
                  Roll No.{" "}
                  {
                    getStudentInternalEvaluationTestDetail?.student
                      ?.studentROLLNO
                  }
                </p>
              </div>
            </div>
            <div className={style.studentMCQButtons}>
              {allQuestion?.map((q, index) => (
                <div
                  className={
                    givenQuestionId?.includes(q?._id) || indexCount === index
                      ? indexCount === index
                        ? style.qButtonActiveBlue
                        : style.qButtonActive
                      : style.qButton
                  }
                  onClick={() => setIndexCount((count) => (count = index))}
                >
                  {index + 1}
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default GiveStudentInternalEvaluation;

const BUTTON_WRAPPER_STYLES = {
  position: "relative",
  zIndex: 1,
};
